.fullscreen-image {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9); /* Warna latar belakang hitam semi-transparan */
    z-index: 9999; /* Pastikan gambar berada di atas elemen lain */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .fullscreen-image img {
    max-width: 100%;
    max-height: 100%;
  }